/* General App container */
.App {
  text-align: center;
  background-color: #f0f0f0; /* Fallback background for content area */
}

/* Hero Section - Fills the entire screen */
.hero-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;

  /* 
    Placeholder background image. 
    Replace this URL with your own image in /react-app/public/images/ or use an external URL.
  */
  
  background-size: cover;
  background-position: center;
}

.hero-text h1 {
  font-size: 3.5rem;
  font-weight: bold;
  margin: 0;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}

.hero-text p {
  font-size: 1.25rem;
  margin-top: 1rem;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.7);
}

/* Content Section - Appears below the hero when scrolling */
.content-section {
  padding: 5rem 4rem; /* 좌우 여백 유지 */
  width: 100%; /* 화면 폭을 채우도록 설정 */
  margin: 0 auto;
  color: #333;
  background-color: #fff;
  line-height: 1.8;
}

.content-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  border-bottom: 2px solid #f0f0f0;
  padding-bottom: 1rem;
}

/* Category Grid Styles */
.categories-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.category-card {
  background-color: #f9f9f9;
  border: 1px solid #eee;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease-in-out;
}

.category-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.category-card h3 {
  font-size: 1.5rem;
  margin-top: 0;
  margin-bottom: 1rem;
}

.category-card h3 a {
  text-decoration: none;
  color: #007bff;
}

.category-card h3 a:hover {
  text-decoration: underline;
}

.category-card p {
  font-size: 0.9rem;
  color: #666;
}
